<template>
  <div class="console">
    <a-config-provider locale="enUS">
      <a-card :style="{ width: '100%', height: '100%', position : 'absolute' }">
        <a-page-header
            :style="{ background: 'var(--color-bg-2)' }"
            title="Small Scheduler"
            :show-back="false"
        >
        </a-page-header>

        <a-row :gutter="16" style="margin-left: 10px;margin-right: 10px;margin-top: 10px">
          <a-col :span="6">
            <a-input v-model="viewData.taskQuery.name" placeholder="HelloWorld" >
              <template #prepend>
                Name
              </template>
            </a-input>
          </a-col>
          <a-col :span="5">
            <a-select :style="{width:'100%'}" v-model="viewData.taskQuery.tag" placeholder="Tag" :options="viewData.tagList" allow-clear allow-search>
            </a-select>
          </a-col>
          <a-col :span="5">
            <a-select :style="{width:'100%'}" v-model="viewData.taskQuery.cron" placeholder="Cron" :options="viewData.cronList" allow-clear allow-search>
            </a-select>
          </a-col>
          <a-col :span="4">
            <a-select :style="{width:'100%'}" v-model="viewData.taskQuery.statusDesc" placeholder="Status" allow-clear>
              <a-option>Running</a-option>
              <a-option>Paused</a-option>
            </a-select>
          </a-col>
          <a-col :span="2">
            <a-button style="width: 100%;font-weight: 600" type="outline" @click="searchTask">
              <template #icon>
                <icon-search />
              </template>
              Search
            </a-button>
          </a-col>
          <a-col :span="2">
            <a-button style="width: 100%;font-weight: 600" type="outline" status="warning" @click="handleCreateClick">
              <template #icon>
                <icon-folder-add />
              </template>
              Create
            </a-button>
          </a-col>
        </a-row>

        <a-modal width="50%" v-model:visible="editVisible" @cancel="handleEditCancel" :on-before-ok="handleEditBeforeOk" okText="Save" cancelText="Exit" unmountOnClose>
          <template #title>
            <div v-if="viewData.taskCommand.id > 0">
              {{ viewData.taskCommand.name }}
            </div>
            <div v-if="viewData.taskCommand.id <= 0">
              New Task
            </div>
          </template>
          <div>
            <a-form :model="viewData.taskCommand">
              <a-form-item field="name" label="Name">
                <a-input v-model="viewData.taskCommand.name" placeholder="HelloWorld" />
              </a-form-item>
              <a-form-item field="tag" label="Tag">
                <a-auto-complete v-model="viewData.taskCommand.tag" :data="viewData.tagList" placeholder="default" strict />
              </a-form-item>
              <a-form-item field="cron" label="Cron">
                <a-auto-complete v-model="viewData.taskCommand.cron" :data="viewData.cronList" placeholder="*/30 * * * * *" strict />
              </a-form-item>
              <a-form-item field="method" label="Method">
                <a-radio-group type="button" v-model="viewData.taskCommand.method" @change="methodChange">
                  <a-radio value="GET">GET</a-radio>
                  <a-radio value="POST">POST</a-radio>
                  <a-radio value="PUT">PUT</a-radio>
                  <a-radio value="PATCH">PATCH</a-radio>
                  <a-radio value="DELETE">DELETE</a-radio>
                </a-radio-group>
              </a-form-item>
              <a-form-item field="url" label="Main URL">
                <a-input v-model="viewData.taskCommand.url" placeholder="http://127.0.0.1:8080/main/job" />
              </a-form-item>
              <a-form-item field="backupUrl" label="Backup URL">
                <a-input v-model="viewData.taskCommand.backupUrl" placeholder="http://127.0.0.1:9090/backup/job" />
              </a-form-item>
              <a-form-item field="retry" label="Retry">
                <a-input-number :style="{width:'300px'}" v-model="viewData.taskCommand.retryMax" :default-value="0" mode="button" class="input-demo" >
                  <template #prefix>
                    Max
                  </template>
                </a-input-number>
                <a-input-number :style="{width:'300px', marginLeft: '10px'}" v-model="viewData.taskCommand.retryCycle" :default-value="0" mode="button" class="input-demo" >
                  <template #prefix>
                    Cycle
                  </template>
                  <template #suffix>
                    ms
                  </template>
                </a-input-number>
              </a-form-item>
              <a-form-item field="header" label="Header">
                <a-button @click="handleHeaderAdd">+</a-button>
              </a-form-item>
              <a-form-item v-for="(item,index) of viewData.taskCommand.headerList" :field="`header[${index}].value`" :key="index">
                <a-input v-model="item.key" placeholder="key" />
                <a-input style="margin-left: 10px" v-model="item.value" placeholder="value" />
                <a-button @click="handleHeaderDelete(index)" :style="{marginLeft:'10px'}" type="primary" status="danger">-</a-button>
              </a-form-item>
              <a-form-item field="body" label="Body">
                <a-textarea v-model="viewData.taskCommand.body" placeholder="{}" allow-clear auto-size/>
              </a-form-item>
            </a-form>
          </div>
        </a-modal>

        <a-spin :loading="viewData.loading">
          <a-table size="small" :columns="taskColumns" :data="viewData.taskList" style="margin: 20px" :pagination="false" column-resizable>

            <template #status="{ record }">
              <div v-if="record.status === 1">
                <a-tag bordered color="green">Running</a-tag>
              </div>
              <div v-if="record.status === 2">
                <a-tag bordered color="orangered">Paused</a-tag>
              </div>
            </template>

            <template #updateAt="{ record }">
              {{ timestampToTime(record.updatedAt) }}
            </template>

            <template #optional="{ record }">

              <!-- 暂停 -->
              <a-popconfirm content="Are you sure?" type="warning" okText="Yes" cancelText="No" @ok="handlePauseClick(record)">
                <a-button size="large" v-if="record.status === 1" status="warning">
                  <template #icon>
                    <icon-pause />
                  </template>
                </a-button>
              </a-popconfirm>

              <!-- 启动 -->
              <a-popconfirm content="Are you sure?" type="warning" okText="Yes" cancelText="No" @ok="handleRunClick(record)">
                <a-button size="large" v-if="record.status === 2" status="success">
                  <template #icon>
                    <icon-play-arrow-fill />
                  </template>
                </a-button>
              </a-popconfirm>

              <!-- 记录 -->
              <a-button size="large" style="margin-left: 5px" @click="handleRecordClick(record)">
                <template #icon>
                  <icon-list />
                </template>
              </a-button>

              <!-- 手动执行 -->
              <a-popconfirm content="Manually execute?" type="warning" okText="Yes" cancelText="No" @ok="handleExecuteClick(record)">
                <a-button size="large" style="margin-left: 5px;border: 1px solid;color: #b27600" type="text">
                  <template #icon>
                    <icon-bug />
                  </template>
                </a-button>
              </a-popconfirm>

              <!-- 编辑 -->
              <a-button size="large" style="margin-left: 5px" type="primary" @click="handleEditClick(record)">
                <template #icon>
                  <icon-settings />
                </template>
              </a-button>

              <!-- 删除 -->
              <a-popconfirm content="Are you sure?" type="warning" okText="Yes" cancelText="No" @ok="handleDeleteClick(record)">
                <a-button size="large" style="margin-left: 5px" type="primary" status="danger">
                  <template #icon>
                    <icon-delete />
                  </template>
                </a-button>
              </a-popconfirm>

            </template>
          </a-table>
          <a-pagination :total="viewData.taskTotal" @change="changePage" style="margin: 10px" :current="viewData.taskQuery.taskPageIndex" :page-size="viewData.taskQuery.taskPageSize"/>
        </a-spin>

        <a-modal width="70%" v-model:visible="recordVisible" :hideCancel="true" :footer="false" okText="Save" cancelText="Exit" unmountOnClose>
          <template #title>
            {{ viewData.taskName }}
          </template>
          <div>
            <a-row class="grid-demo">
              <a-col :span="12">
                <a-space size="large">
                  <a-statistic title="Total" :value="viewData.recordTotal" show-group-separator />
                </a-space>
              </a-col>
              <a-col :span="12">
                <p style="text-align: right;">Sharding:&nbsp;&nbsp;<a-tag>{{ viewData.recordSharding }}</a-tag></p>
                <p style="text-align: right;">Table:&nbsp;&nbsp;<a-tag>record_{{ viewData.recordSharding }}</a-tag></p>
              </a-col>
            </a-row>
            <a-table :columns="recordColumns" :data="viewData.recordList" column-resizable>
              <template #executedAt="{ record }">
                {{ timestampToTime(record.executedAt) }}
              </template>
              <template #timeCost="{ record }">
                {{ record.timeCost }} ms
              </template>
              <template #type="{ record }">
                <div v-if="record.isBackup === 1">
                  Backup
                </div>
                <div v-if="record.isBackup === 0">
                  Main
                </div>
              </template>
            </a-table>
          </div>
        </a-modal>
      </a-card>
    </a-config-provider>
  </div>
</template>

<script>
import { ref, reactive } from 'vue';
import axios from 'axios'
import { Message } from '@arco-design/web-vue';
import { IconPause, IconPlayArrowFill, IconList, IconSettings, IconDelete, IconSearch, IconFolderAdd, IconBug } from '@arco-design/web-vue/es/icon';

export default {
  components: { IconPause, IconPlayArrowFill, IconList, IconSettings, IconDelete, IconSearch, IconFolderAdd, IconBug },
  name: 'ConsolePage',
  mounted () {
    this.searchTask()
  },
  setup() {
    const taskColumns = [{
      title: 'ID',
      dataIndex: 'id',
      ellipsis: true,
      tooltip: true,
      width: 80
    }, {
      title: 'Tag',
      dataIndex: 'tag',
      ellipsis: true,
      tooltip: true,
      width: 120
    }, {
      title: 'Name',
      dataIndex: 'name',
      ellipsis: true,
      tooltip: true,
      width: 220
    }, {
      title: 'Cron',
      dataIndex: 'cron',
      ellipsis: true,
      tooltip: true,
      width: 150
    }, {
      title: 'Status',
      slotName: 'status',
      ellipsis: true,
      tooltip: true,
      width: 100
    }, {
      title: 'Updated At',
      slotName: 'updateAt',
      ellipsis: true,
      tooltip: true,
      width: 180
    }, {
      title: 'Optional',
      slotName: 'optional',
      fixed: 'right',
      width: 300
    }];

    const recordColumns = [{
      title: 'Executed At',
      slotName: 'executedAt',
      ellipsis: true,
      tooltip: true,
      width: 60
    }, {
      title: 'Result',
      dataIndex: 'result',
      ellipsis: true,
      tooltip: true,
      width: 150
    }, {
      title: 'Code',
      dataIndex: 'code',
      ellipsis: true,
      tooltip: true,
      width: 30
    }, {
      title: 'Cost',
      slotName: 'timeCost',
      ellipsis: true,
      tooltip: true,
      width: 30
    }, {
      title: 'Retry',
      dataIndex: 'retryCount',
      ellipsis: true,
      tooltip: true,
      width: 30
    }, {
      title: 'Type',
      slotName: 'type',
      ellipsis: true,
      tooltip: true,
      width: 30
    }];

    const viewData = reactive({
      taskList: [],
      recordList: [],
      tagList: [],
      cronList: [],
      taskTotal: 0,
      recordTotal: 0,
      taskCommand: {
        id: 0,
        name: '',
        tag: '',
        cron: '',
        method: 'GET',
        url: '',
        backupUrl: '',
        body: '',
        retryMax: 0,
        retryCycle: 0,
        headerList: [],
        header: {},
        status: 0,
      },
      taskQuery: {
        name: '',
        cron: '',
        tag: '',
        statusDesc: '',
        taskPageIndex: 1,
        taskPageSize: 20,
      },
      loading: false,
      recordSharding: todaySharding(),
    })

    function todaySharding() {
      let dateStr = new Date(Date.now()).toISOString().split("T")[0]
      let dateArr = dateStr.split("-")
      return dateArr[0]+'_'+dateArr[1]
    }

    const handleRunClick = (task) => {
      axios.put(getRootUrl()+'/task/'+task.id, {
        status: 1
      }).then(() => {
        Message.success({
          content: "operation was successful",
          closable: true
        })
        searchTask()
      }).catch((error) => {
        Message.error({
          content: error.response.data.msg,
          closable: true
        })
      })
    }

    const handlePauseClick = (task) => {
      axios.put(getRootUrl()+'/task/'+task.id, {
        status: 2
      }).then(() => {
        Message.success({
          content: "operation was successful",
          closable: true
        })
        searchTask()
      }).catch((error) => {
        Message.error({
          content: error.response.data.msg,
          closable: true
        })
      })
    }

    const handleExecuteClick = (task) => {
      axios.get(getRootUrl()+'/execute/'+task.id, {}).then(() => {
        Message.success({
          content: "operation was successful",
          closable: true
        })
      }).catch((error) => {
        Message.error({
          content: error.response.data.msg,
          closable: true
        })
      })
    }

    const handleEditClick = (task) => {
      viewData.taskCommand.id = task.id
      viewData.taskCommand.name = task.name
      viewData.taskCommand.tag = task.tag
      viewData.taskCommand.cron = task.cron
      viewData.taskCommand.method = task.method
      viewData.taskCommand.url = task.url
      viewData.taskCommand.backupUrl = task.backupUrl
      viewData.taskCommand.body = task.body
      viewData.taskCommand.retryMax = task.retryMax
      viewData.taskCommand.retryCycle = task.retryCycle
      viewData.taskCommand.header = task.header
      viewData.taskCommand.headerList = []
      if (task.header != null) {
        Object.keys(task.header).forEach(function(key){
          viewData.taskCommand.headerList.push({key: key, value: task.header[key]})
        })
      }
      viewData.taskCommand.status = task.status
      editVisible.value = true;
    }

    const handleRecordClick = (task) => {
      viewData.taskName = task.name
      let params = {
        taskId: task.id,
        sharding: viewData.recordSharding,
        pageIndex: 1,
        pageSize: 200
      }
      axios.get(getRootUrl()+'/records?'+Object.keys(params).map(key => key + '=' + params[key]).join('&'), {}).then((data) => {
        viewData.recordList = data.data.list
        viewData.recordTotal = task.total
      }).catch((error) => {
        Message.error({
          content: error.response.data.msg,
          closable: true
        })
      })
      recordVisible.value = true
    }

    const handleDeleteClick = (task) => {
      axios.delete(getRootUrl()+'/task/'+task.id, {}).then(() => {
        Message.success({
          content: "operation was successful",
          closable: true
        })
        searchTask()
      }).catch((error) => {
        Message.error({
          content: error.response.data.msg,
          closable: true
        })
      })
    }

    const editVisible = ref(false);
    const recordVisible = ref(false);

    const handleEditBeforeOk = async () => {
      let ok = true
      viewData.taskCommand.header = {}
      if (viewData.taskCommand.headerList != null && viewData.taskCommand.headerList.length > 0) {
        for (let i=0;i<viewData.taskCommand.headerList.length;i++) {
          viewData.taskCommand.header[viewData.taskCommand.headerList[i].key] = viewData.taskCommand.headerList[i].value
        }
      }
      if (viewData.taskCommand.id > 0) {
        await axios.put(getRootUrl() + '/task/' + viewData.taskCommand.id, viewData.taskCommand).then(() => {
          Message.success({
            content: "operation was successful",
            closable: true
          })
          searchTask()
        }).catch((error) => {
          ok = false
          Message.error({
            content: error.response.data.msg,
            closable: true
          })
        })
      } else {
        await axios.post(getRootUrl() + '/task', viewData.taskCommand).then(() => {
          Message.success({
            content: "operation was successful",
            closable: true
          })
          searchTask()
        }).catch((error) => {
          ok = false
          Message.error({
            content: error.response.data.msg,
            closable: true
          })
        })
      }
      return ok;
    }

    const handleCreateClick = () => {
      viewData.taskCommand.id = 0
      viewData.taskCommand.name = ''
      viewData.taskCommand.tag = ''
      viewData.taskCommand.cron = ''
      viewData.taskCommand.method = 'GET'
      viewData.taskCommand.url = ''
      viewData.taskCommand.backupUrl = ''
      viewData.taskCommand.body = ''
      viewData.taskCommand.retryMax = 0
      viewData.taskCommand.retryCycle = 0
      viewData.taskCommand.headerList = []
      viewData.taskCommand.header = {}
      viewData.taskCommand.status = 2
      editVisible.value = true;
    }

    const handleEditCancel = () => {
      editVisible.value = false;
    }

    const handleHeaderAdd = () => {
      viewData.taskCommand.headerList.push({key: '', value: ''})
    };
    const handleHeaderDelete = (index) => {
      viewData.taskCommand.headerList.splice(index, 1)
    }

    function changePage (current) {
      if (current === undefined || current === null || current === 0) {
        viewData.taskQuery.taskPageIndex = 1
      } else {
        viewData.taskQuery.taskPageIndex = current
      }
      searchTask()
    }

    function searchTask () {
      viewData.loading = true
      initTag()
      initCron()
      let params = {
        name: viewData.taskQuery.name,
        cron: viewData.taskQuery.cron,
        tag: viewData.taskQuery.tag,
      }
      if (viewData.taskQuery.statusDesc === 'Running') {
        params.status = 1
      } else if (viewData.taskQuery.statusDesc === 'Paused') {
        params.status = 2
      } else {
        params.status = 0
      }
      params.pageIndex = viewData.taskQuery.taskPageIndex
      params.pageSize = viewData.taskQuery.taskPageSize
      axios.get(getRootUrl()+'/tasks?'+Object.keys(params).map(key => key + '=' + params[key]).join('&'), {}).then((data) => {
        viewData.taskTotal = data.data.total
        viewData.taskList = data.data.list
        viewData.loading = false
      }).catch((error) => {
        viewData.loading = false
        Message.error({
          content: error.response.data.msg,
          closable: true
        })
      })
    }

    function initTag () {
      let params = {
        status: 0,
      }
      axios.get(getRootUrl()+'/tags?'+Object.keys(params).map(key => key + '=' + params[key]).join('&'), {}).then((data) => {
        if (data.data != null && data.data.length > 0) {
          viewData.tagList = []
          for (let i=0;i<data.data.length;i++) {
            viewData.tagList.push(data.data[i].tag)
          }
        }
      }).catch((error) => {
        Message.error({
          content: error.response.data.msg,
          closable: true
        })
      })
    }

    function initCron () {
      let params = {
        status: 0,
      }
      axios.get(getRootUrl()+'/crons?'+Object.keys(params).map(key => key + '=' + params[key]).join('&'), {}).then((data) => {
        if (data.data != null && data.data.length > 0) {
          viewData.cronList = []
          for (let i=0;i<data.data.length;i++) {
            viewData.cronList.push(data.data[i].cron)
          }
        }
      }).catch((error) => {
        Message.error({
          content: error.response.data.msg,
          closable: true
        })
      })
    }

    const methodChange = () => {
      if (viewData.taskCommand.method === 'POST' || viewData.taskCommand.method === 'PUT' || viewData.taskCommand.method === 'PATCH') {
        if (viewData.taskCommand.headerList.length === 0) {
          viewData.taskCommand.headerList.push({key: 'Content-Type', value: 'application/json'})
        } else {
          for (let i=0;i<viewData.taskCommand.headerList.length;i++) {
            if (viewData.taskCommand.headerList[i].key === 'Content-Type' && viewData.taskCommand.headerList[i].value === 'application/json') {
              return
            }
          }
          viewData.taskCommand.headerList.push({key: 'Content-Type', value: 'application/json'})
        }
      }
      if (viewData.taskCommand.method === 'GET' || viewData.taskCommand.method === 'DELETE') {
        if (viewData.taskCommand.headerList.length > 0) {
          for (let i=0;i<viewData.taskCommand.headerList.length;i++) {
            if (viewData.taskCommand.headerList[i].key === 'Content-Type' && viewData.taskCommand.headerList[i].value === 'application/json') {
              handleHeaderDelete(i)
            }
          }
        }
      }
    }

    document.body.setAttribute('arco-theme', 'dark')

    function timestampToTime(timestamp) {
      timestamp = timestamp ? timestamp : null;
      let date = new Date(timestamp);//时间戳为10位需*1000，时间戳为13位的话不需乘1000
      let Y = date.getFullYear() + '-';
      let M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
      let D = (date.getDate() < 10 ? '0' + date.getDate() : date.getDate()) + ' ';
      let h = (date.getHours() < 10 ? '0' + date.getHours() : date.getHours()) + ':';
      let m = (date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes()) + ':';
      let s = date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds();
      return Y + M + D + h + m + s;
    }

    function getRootUrl() {
      // return "api/v1"
      const urls = window.location.href.split("/web/");
      let rootUrl = ""
      for (let i=0; i<urls.length-1; i++) {
        rootUrl = rootUrl + urls[i]
      }
      return rootUrl
    }

    return {
      taskColumns,
      recordColumns,
      viewData,
      editVisible,
      recordVisible,
      changePage,
      handleHeaderAdd,
      handleExecuteClick,
      handleHeaderDelete,
      handleRunClick,
      handlePauseClick,
      handleEditClick,
      handleRecordClick,
      handleDeleteClick,
      handleEditBeforeOk,
      handleEditCancel,
      searchTask,
      methodChange,
      handleCreateClick,
      timestampToTime
    }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>
